<template>
<div class="container">
  <div class="head_block">
    <div class="head back_team"><div class="head_text">Test</div>
    </div>
    <p>1.первая буква большая после ввода второй буквы</p>
    <input @input="ToUpper1()" v-model="filter1" class="main_input" type="text" placeholder="Поиск">
    <p>2.первая буква большая всегда</p>
    <input @input="ToUpper3()" v-model="filter3" class="main_input" type="text" placeholder="Поиск">
    <p>3.первая буква большая после смены инпута если больше 2х букв</p>
    <input @change="ToUpper2()" v-model="filter2" class="main_input" type="text" placeholder="Поиск">
    <p>4.первая буква большая после смены инпута</p>
    <input @change="ToUpper4()" v-model="filter4" class="main_input" type="text" placeholder="Поиск">
  </div>
</div>
</template>

<script>
export default {
  data(){
    return {
      img: 'https://avatars.mds.yandex.net/i?id=31a35c6589376394432599b8f8d8ef3fabfb6987-6390812-images-thumbs&n=13',
      filter1: '',
      filter2: '',
      filter3: '',
      filter4: '',
      team: [],
    }
  },
  methods:  {
    ToUpper1() {
      if (this.filter1.length > 1) {
        this.filter1 = this.filter1.charAt(0).toUpperCase() + this.filter1.slice(1)
      }
    },
    ToUpper2() {
      if (this.filter2.length > 1) {
        this.filter2 = this.filter2.charAt(0).toUpperCase() + this.filter2.slice(1)
      }
    },
    ToUpper3() {
        this.filter3 = this.filter3.charAt(0).toUpperCase() + this.filter3.slice(1)
    },
    ToUpper4() {
        this.filter4 = this.filter4.charAt(0).toUpperCase() + this.filter4.slice(1)
    }
  },
}
</script>
